* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #D5D287;
}

#main-div {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#main-div h1 {
  color: #d6d016;
  font-family: 'Lobster', cursive;
  font-size: 4vw;
  background-color: #333100;
  border-radius: 30px;
  border: 0;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 5px;
  width: 97%;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
}

#wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

#form, #tasks {
  flex: 1;
  width: 50%;
}

#task {
  margin-bottom: 13px;
  margin-left: 20px;
  padding: 15px;
  padding-left: 30px;
  border-radius: 30px;
  /* background: linear-gradient(45deg, #333100, #807D26); */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.8);
  font-family: 'Courier New', Courier, monospace;
}


.task-not-done {
  background: linear-gradient(45deg, #242300, #1f1e00);
  border: 3px solid #333;
  color: #d6d016;
}

.task-done {
  background: linear-gradient(45deg, #333100, #807D26);
  color: #FFFFFF;
}

#task button {
  width: 5vw;
  height: 2vw;
  border-radius: 10px;
  background-color: #D5D287;
  border: 0;
}

#task input {
  width: 3vw;
  height: 1.8vw;
  padding-top: 1vw;
}

#task p {
  width: 80%;
  overflow: hidden;
  word-wrap: break-word;
}

#task h2 {
  margin-bottom: 10px;
}

#task-status, #task-details {
  margin-bottom: 8px;
}

#check-and-delete {
  display: flex;
  align-items: center;
  float: right;
  position: relative;
  bottom: 83px;
}

.pagination {
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  margin-top: auto;
}

.pagination button {
  height: 2vw;
  width: 5vw;
  background-color: #807D26;
  border-radius: 20px;
}

#form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

#form input[type='text'] {
  background-color: #d6d5aa;
  width: 30vw;
  height: 3vw;
  border-radius: 10px;
  border: 1px solid #333100;
  margin-bottom: 10px;
  padding: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
}

#form textarea {
  width: 30vw;
  height: 8vw;
  border-radius: 10px;
  border: 1px solid #333100;
  margin-bottom: 10px;
  padding: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
  background-color: #d6d5aa;
}

#form input[type="date"] {
  padding: 10px;
  border: 1px solid #333100;
  border-radius: 4px;
  font-size: 16px;
  color: #333;
  background-color: #fff;
  outline: none;
  cursor: pointer;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
  background-color: #d6d5aa;
}

/* #form input[type="date"]::-webkit-inner-spin-button,
#form input[type="date"]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
} */

#form input[type="date"]:focus {
  border-color: #807D26;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

#form button {
  margin-top: 20px;
  height: 3vw;
  width: 7vw;
  background-color: #807D26;
  border-radius: 20px;
  border: 0;
  color: white;
}

#form button:hover {
  border: 3px solid #333100;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
}

#form button:active {
  background-color: white;
  color: black;
  border: 2px solid #333100;
}


input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.custom-checkbox {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #ccc;
  border-radius: 4px;
  position: relative;
}

.custom-checkbox::after {
  content: "";
  position: absolute;
  display: none;
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

input[type="checkbox"]:checked + .custom-checkbox {
  background-color: green;
}

input[type="checkbox"]:checked + .custom-checkbox::after {
  display: block;
}

label {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}